import uniDrawer from "../../components/uni-drawer/uni-drawer.vue"
const app = getApp()
import cookies from '../../vendor/weapp-cookie/dist/weapp-cookie'
export default {
	components: {uniDrawer},
	data() {
		return {
			drawer: false,
			week: "14",
			days: [{
				weekday: "周日",
				dates: '',
				isToday: false
			},{
				weekday: "周一",
				dates: '',
				isToday: false
			},{
				weekday: '周二',
				dates: '',
				isToday: false
			},{
				weekday: '周三',
				dates: '',
				isToday: false
			},{
				weekday: '周四',
				dates: '',
				isToday: false
			},{
				weekday: '周五',
				dates: '',
				isToday: false
			},{
				weekday: '周六',
				dates: '',
				isToday: false
			}],
			classTimes: [{
				nthClass: "1",
				time: "8:20"
			},{
				nthClass: "2",
				time: "9:15"
			},{
				nthClass: "3",
				time: "10:20"
			},{
				nthClass: "4",
				time: "11:15"
			},{
				nthClass: "5",
				time: "14:30"
			},{
				nthClass: "6",
				time: "15:25"
			},{
				nthClass: "7",
				time: "16:30"
			},{
				nthClass: "8",
				time: "17:25"
			},{
				nthClass: "9",
				time: "19:30"
			},{
				nthClass: "10",
				time: "20:25"
			}],
			weeks: 13,
			kcs: []
		}
	},
	onLoad() {
		// 渲染星期几和日期
		var that = this
		var date = new Date();
		var weekday = new Date().getDay() // 距离周日有几天
		var strDate = date.getDate() // 当前日期
		strDate = strDate - weekday // 本周日日期
		for(var t=0; t < 7; t++){
			that.days[t].dates = strDate
			strDate++
		}
		that.days[weekday].isToday = true
		// console.log(Boolean(uni.getStorageSync('timeTable')))
		// 获取课表缓存
		var timeTable = uni.getStorageSync('timeTable')
		if(timeTable){
			// 解析渲染课表
			console.log(JSON.parse(timeTable))
			var parseTimeTable = JSON.parse(timeTable)
			var kcs = that.jiexiTimeTable(parseTimeTable)
			that.kcs = kcs
		}else{
			// 课表不存在
			uni.showModal({
				title: "尚未导入课表",
				content: "是否现在导入课表",
				success(res) {
					if(res.confirm){
						uni.showLoading({
							title: '导入中'
						})
						// 如果尚未登陆
						if(!app.globalData.userInfo){
							uni.hideLoading()
							uni.showToast({
								icon: "none",
								title: "您尚未授权登录",
								duration: 2000
							})
						}else{
							uni.requestWithCookie({
								url: app.globalData.host + app.globalData.apiVersion + "api/timetable/" + "?TimeName=2019-2020-1&openid=" + app.globalData.userInfo.openid,
								success: function(e){
									console.log('信息：', e)
									if(e.statusCode==200 && e.data.code==0){
										if(e.data.data){
											// 解析课表
											var kcs = that.jiexiTimeTable(e.data.data)
											that.kcs = kcs
											// 存入缓存
											var data = JSON.stringify(e.data.data)
											console.log(data)
											uni.setStorageSync('timeTable', data)
											uni.hideLoading()
											uni.showToast({
												title: "导入成功"
											})
										}else{
											uni.hideLoading()
											uni.showToast({
												title: "导入失败"
											})
										}
									}
								}
							})
						}
					}
				}
			})
		}
	},
	methods: {
		// 解析课表
		jiexiTimeTable(parseTimeTable){
			var classes = []
			var resolve = []
			for(var index = 0; index < parseTimeTable.length; index++){
				let timeClass = parseTimeTable[index]
				let classTime = Object.keys(timeClass)
				let someClasses = Object.values(timeClass)
				let weekTimeClass = []
				for(var id = 0; id < someClasses[0].length; id++){
					let data = {}
					let weekday = Object.keys(someClasses[0][id])
					let classes = Object.values(someClasses[0][id])
					if(classes[0][3].search('14') != -1){
						data[weekday[0]] = classes[0]
						weekTimeClass.push(data)
					}
				}
				let dt = {}
				dt[classTime[0]] = weekTimeClass
				resolve.push(dt)
			}
			console.log(resolve)
			var kcs = []
			var w = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"]
			for(var x = 0; x < resolve.length; x++){
				var cles = Object.values(resolve[x])[0]
				// console.log(cles)
				var weeks = []
				var kc = []
				for(var e = 0; e < cles.length; e++){
					var kecheng = Object.values(cles[e])[0]
					kc.push(kecheng)
					var week = Object.keys(cles[e])[0]
					weeks.push(week)
				}
				console.log(kc)
				// console.log(weeks)
				for(var t = 0; t < 7; t++){
					var txt = ''
					for(var y = 0; y < kc.length; y++){
						if(weeks[y]==w[t]){
							txt += kc[y][0] + '\n@' + kc[y][4]
						}
					}
					kcs.push(txt)
				}
			}
			console.log(kcs)
			return kcs
		},
		displayDrewer(){
			var that = this
			that.drawer = true
		},
		closeDrawer(){
			var that = this
			that.drawer = false
		}
	}
}
